.form {
  background-color: $gray-light;
  margin-left: 5px;
  margin-right: 5px;
  position: relative;
  box-shadow: inset 0 0 0 8px $gray-medium;
  margin-bottom: 49px;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 20%;
    width: 40%;
    height: 8px;
    background-color: $gray-light;
  }

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 20%;
    width: 40%;
    height: 8px;
    background-color: $gray-light;
  }

  @media (min-width: $screen-sm) {
    margin-bottom: 60px;
  }

  @media (min-width: $screen-lg) {
    margin-left: 2.5%;
    margin-right: 2.5%;
    margin-bottom: 51px;
  }

  &__inner {
    padding-top: 60px;
    padding-left: 20px;
    padding-right: 21px;
    padding-bottom: 91px;
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    @media (min-width: $screen-sm) {
      padding-top: 70px;
      padding-left: 25px;
      padding-bottom: 61px;
      padding-right: 15px;
    }

    @media (min-width: $screen-lg) {
      padding-bottom: 74px;
      position: relative;
      width: 1160px;
      margin: 0 auto;
    }

    .field-action {
      order: 4;
      width: 100%;

      @media (min-width: $screen-lg) {
        order: 2;
        width: 30%;
        display: inline-block;
      }
    }
  }

  &__header {
    width: 100%;
    order: 1;
  }

  &__header-title {
    margin-top: 0;
    margin-bottom: 21px;
    font-size: $font-large;
    font-weight: 700;
  }

  &__header-describtion {
    font-size: $font-medium;
    line-height: 24px;
    margin-top: 0;
    margin-bottom: 30px;

    @media (min-width: $screen-lg) {
      margin-bottom: 17px;
    }
  }

  & .field-text__wrap {
    order: 2;
    width: 100%;

    @media (min-width: $screen-lg) {
      width: 70%;
      display: inline-block;
    }
  }

  & .field-text {

    @media (min-width: $screen-sm) {
      display: inline-block;
      margin-right: 20px;
    }


/* stylelint-disable*/
    &__input {
      background-color: transparent;
      border-top: 0;
      border-left: 0;
      border-right: 0;
      border-bottom: solid 4px $gray-stronger;
      width: 100%;
      margin-bottom: 46px;
      font-family: 'Montserrat';
      font-size: $font-medium;

      &.error {
        border-bottom: solid 4px $color-error;
      }

      @media (min-width: $screen-sm) {
        width: 325px;
        margin-bottom: 38px;
      }

      @media (min-width: $screen-lg) {
        width: 360px;
        padding-top: 30px;
        margin-bottom: 28px;

        &:hover {
          border-bottom: solid 4px $color-normal;
        }

        &:focus {
          border-bottom: solid 4px $color-hover;
        }
      }
    }
    /* stylelint-enable*/
  }
}



.field-checkbox {

  order: 3;

  &__label {
    display: block;
    position: relative;
    padding-left: 28px;
    user-select: none;
    cursor: pointer;

    & input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
    }

    & input:checked ~ .checkmark {
      background-color: $gray-medium;
      border: 0;
    }

    & input:checked ~ .checkmark:after {
      display: block;
    }

    & .checkmark {
      position: absolute;
      top: 4px;
      left: 0;
      width: 20px;
      height: 20px;
      border: 4px solid $gray-stronger;

      &:hover {
        border: 4px solid $color-hover;
      }

      &:after {
        content: '';
        position: absolute;
        display: none;
        left: 5px;
        top: 2px;
        width: 10px;
        height: 13px;
        border: solid $color-hover;
        border-width: 0 3px 3px 0;
        transform: rotate(45deg);
      }
    }
  }

  &__text {
    font-size: $font-small;
    line-height: 24px;
    display: inline-block;

    @media (min-width: $screen-sm) {
      width: 380px;
      display: inline-block;
    }

    & a {
      color: $link-color;
    }
  }
}


.form__inner .field-action .btn {
  height: 50px;
  width: 100%;
  border-right: 2px;
  background-color: $dark-dark;
  border: 0;
  color: $light-light;
  font-size: $font-small;
  font-weight: 700;
  margin-top: 29px;


  @media (min-width: $screen-sm) {
    width: 331px;
    margin-top: 43px;
  }

  @media (min-width: $screen-lg) {
    margin-top: 0;

    &:hover {
      background-color: $color-hover;
    }

    &:active {
      background-color: $color-pressed;
    }
  }
}


#email-error {
  color: $color-error;
  position: absolute;
  left: 30px;
  top: 170px;
}

#agreement-error {
  color: $color-error;
  position: absolute;
  left: 0;
  bottom: -30px;
}

#agreement-error ~ .checkmark {
  border: 4px solid $color-error;
}
